* {
  box-sizing: border-box;
}

body {
  color: #4a4a4a;
  background-color: #f9f5f2;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

h1 {
  color: #e44d26;
  text-align: center;
  margin-top: 20px;
}

.js-search {
  max-width: 600px;
  background-color: #fff;
  border-radius: 8px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

label {
  margin-bottom: 10px;
  display: block;
}

input[type="text"], select {
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 5px;
  margin-bottom: 15px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #e44d26;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
}

button:hover {
  background-color: #d0451b;
}

.cuisine-list {
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 0;
  list-style: none;
  display: flex;
}

.recipe {
  width: 30%;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin: 10px;
  padding: 15px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.recipe img {
  max-width: 100%;
  height: auto;
  border-radius: 4px;
}

.title-recipe {
  color: #e44d26;
  margin-top: 10px;
  font-size: 1.2em;
}

.link-recipe {
  color: #3498db;
  margin-top: 10px;
  text-decoration: none;
  display: block;
}

.link-recipe:hover {
  text-decoration: underline;
}

.summary {
  color: #555;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .recipe {
    width: 45%;
  }
}

